<script>
  import { navigate } from "../utils.js";

  export let msg;

  let content = ssb.markdown(msg.value.content.text);

  const reply = ev => {
    let rootId = msg.value.content.root || msg.key;
    let channel = msg.value.content.channel;
    navigate("/compose", { root: rootId, branch: msg.key, channel });
  };
</script>

<style>
  div img.is-image-from-blob {
    max-width: 90%;
  }
</style>

<div class="card-body">
  {@html content}
</div>
<div class="card-footer">
  <div class="columns col-gapless">
    <div class="column col-6">
      <label class="form-switch d-inline">
        <input type="checkbox" />
        <i class="form-icon" />
        Like
      </label>
      {#if msg.value.content.root}
        <span>
          <a href="?thread={encodeURIComponent(msg.value.content.root)}#/thread">(root)</a>
        </span>
      {/if}
      {#if msg.value.content.branch}
        <span>
          <a href="?thread={encodeURIComponent(msg.value.content.branch)}#/thread">(in reply to)</a>
        </span>
      {/if}
    </div>
    <div class="column col-6 text-right">
      <button class="btn" on:click={reply}>Reply</button>
    </div>
  </div>

</div>
